//import React from 'react';
import { useState } from 'react';
import './popup.css';
import {checkCredentials,
  hashString,
  checkUsernameExists,
  registerUser,
} from './Login';

function Popup({ isOpen, onClose, onLogin, onRegister }) {
  const [isLogin, setIsLogin] = useState(true);
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');

  const toggleForm = () => {
    setIsLogin(!isLogin);
    setError('');
    setPassword('');
    setConfirmPassword('');
  };
  
  const handleLoginSubmit = async (e) => {
    e.preventDefault();
    setError('');
    
    if (!username || !password) {
      setError('Пожалуйста, введите логин и пароль');
      return;
    }

    try {
      const hashedPassword = hashString(password);
      const isValid = await checkCredentials(username, hashedPassword);
      
      if (isValid) {
        onLogin(username);
        onClose();
      } else {
        setError('Неверные учетные данные');
      }
    } catch (err) {
      setError('Ошибка при проверке учетных данных');
      console.error('Login error:', err);
    }
  };

  const handleRegisterSubmit = async (e) => {
    e.preventDefault();
    setError('');
    
    if (!username || !password || !confirmPassword) {
      setError('Пожалуйста, заполните все поля');
      return;
    }

    if (password !== confirmPassword) {
      setError('Пароли не совпадают');
      return;
    }

    try {
      const usernameExists = await checkUsernameExists(username);
      if (usernameExists) {
        setError('Ошибка! Пользователь с таким логином уже существует');
        return;
      }

      const hashedPassword = hashString(password);
      const registrationResult = await registerUser(username, hashedPassword, 'user');
      
      if (registrationResult.success) {
        await onRegister();
        onClose();
      } else {
        setError(registrationResult.message || 'Ошибка при регистрации');
      }
    } catch (err) {
      setError(err.message || 'Ошибка при регистрации');
      console.error('Registration error:', err);
    }
  };

  if (!isOpen) {
    return null;
  }

  return (
    <div className="popup">
      <div className="popup-container">
        <div className="popup-body">
          <h2>{isLogin ? 'Вход в аккаунт' : 'Регистрация'}</h2>
          {error && <div className="error-message">{error}</div>}
          <form onSubmit={isLogin ? handleLoginSubmit : handleRegisterSubmit}>
            {isLogin ? (
              <>
                <input 
                  type="text" 
                  placeholder="Логин" 
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                />
                <input 
                  type="password" 
                  placeholder="Пароль" 
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
              </>
            ) : (
              <>
                <input 
                  type="text" 
                  placeholder="Логин" 
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                />
                <input 
                  type="password" 
                  placeholder="Пароль" 
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
                <input 
                  type="password" 
                  placeholder="Подтвердите пароль" 
                  value={confirmPassword}
                  onChange={(e) => {
                    setConfirmPassword(e.target.value);
                    if (e.target.value !== password) {
                      setError('Пароли не совпадают');
                    } else {
                      setError('');
                    }
                  }}
                />
              </>
            )}
            <button type="submit">
              {isLogin ? 'Войти' : 'Зарегистрироваться'}
            </button>
          </form>

          <p onClick={toggleForm} className="toggle-form">
            {isLogin ? 'Нет аккаунта? Зарегистрироваться' : 'Уже есть аккаунт? Войти'}
          </p>

          <div className="popup-close" onClick={onClose}>&#10006;</div>
        </div>
      </div>
    </div>
  );
}

export default Popup;